let merge = (...sources) => Object.assign(...sources);

class WebContextInfo {
    constructor() {
        this.cacheInfo = {};
        this.url();
        this.referrer();
        this.resolution();
        this.browser();
        this.os();
    }

    /**
     * url
     * @returns {string|window.location.href|*}
     */
    url() {
        return this.cacheInfo.url = document.location.href;
    }

    /**
     * 
     * @returns {*}
     */
    referrer() {
        return this.cacheInfo.referrer = document.referrer;
    }

    /**
     * 
     * @returns {string}
     */
    resolution() {
        return this.cacheInfo.resolution = window.screen.width + "*" + window.screen.height;
    }

    /**
     * ()
     * @returns {string}
     */
    os() {
        var ret = "Unknown";
        var sUA = navigator.userAgent.toLowerCase();
        if (sUA.indexOf('win') != -1) {
            ret = "Windows";
        } else if (sUA.indexOf('linux') != -1) {
            ret = 'Linux';
        } else if (sUA.indexOf('mac') != -1) {
            ret = "Mac";
        }
        return this.cacheInfo.os = ret;
    }

    /**
     * 
     * @returns {string}
     */
    browser() {
        let br = {
            type: "Unknown",
            version: "0"
        };
        let explorer = window.navigator.userAgent.toLowerCase();
        if (explorer.indexOf("msie") >= 0) {
            let ver = explorer.match(/msie ([\d.]+)/)[1];
            br = {
                type: "IE",
                version: ver
            };
        } else if (explorer.indexOf("firefox") >= 0) {
            let ver = explorer.match(/firefox\/([\d.]+)/)[1];
            br = {
                type: "Firefox",
                version: ver
            };
        } else if (explorer.indexOf("chrome") >= 0) {
            let ver = explorer.match(/chrome\/([\d.]+)/)[1];
            br = {
                type: "Chrome",
                version: ver
            };
        } else if (explorer.indexOf("opera") >= 0) {
            let ver = explorer.match(/opera.([\d.]+)/)[1];
            br = {
                type: "Opera",
                version: ver
            };
        } else if (explorer.indexOf("Safari") >= 0) {
            let ver = explorer.match(/version\/([\d.]+)/)[1];
            br = {
                type: "Safari",
                version: ver
            };
        }
        return this.cacheInfo.browser = br.type + "," + br.version;
    }
}


let VERSION = "1.0.0";
let levelList = ['log', 'debug', 'info', 'warn', 'error', 'silent'];
let __config = {
    level: "info", //silent(5) error(4)warn(3)info(2)log(1)debug(0),
    tagFilter: "", //tag,
    post: 0, //post
    postContextInfo: 0, //
    postUrl: "" //
};

class log4j {
    constructor(...args) {
        this.version = VERSION;
        this.__config = merge(__config, ...args);
        ["log", 'debug', 'info', 'warn', 'error'].forEach((level) => {
            this[level] = (...args) => {
                this.exec(level, ...args);
            }
        });
    }
    config(...args) {
        if (arguments.length == 0) {
            return this.__config;
        }
        this.__config = merge(__config, ...args);
    }

    /*
     * 
     * ,WARNERROR,WARN
     * 
     * @param {String} level  
     * @param {String} msg 
     * @param {String} tag 
     */
    exec(level, msg, tag) {

        let configLevel = levelList.indexOf(level);
        let outputLevel = levelList.indexOf(this.__config.level);

        if (outputLevel > configLevel)
            return;
        let needLog = false;
        //
        if (!tag) {
            let reg = new RegExp(this.__config.tagFilter);
            needLog = reg.test(tag);
        } else {
            needLog = true;
        }
        if (needLog) {
            console ? console[level](msg) : !0;
            if (msg instanceof Error && this.__config.post) {
                this.post(msg); //
            }
        }
    }
    post(msg) {
        let body = getPostData(msg);
        try {
            $.post(this.__config.postUrl, body);
        } catch (e) {
            console ? console.error(e) : !0;
        }
    }

    getPostData(msg) {
        let body = {};
        if (this.__config.postContextInfo) {
            body = new WebContextInfo();
        }
        if (msg instanceof Error) {
            body = merge(body, {
                name: msg.name,
                message: msg.message,
                stack: msg.stack
            });
        } else {
            body = merge(body, {
                name: "",
                message: msg + "",
                stack: ""
            });
        }
        return body;
    }

    /**/
    static noConflict() {
        window.log4j = oldLog4j;
        return log4j;
    }
}



export default log4j;