// import Constant from '../constant.js'
// import Random from './random/index.js'

import {
    EventBase,
    Event
} from './event.js'

import log4j from './log4j.js'
import Drag from './Drag.js'

let Util = {}
Util.extend = (...args) => {
    Object.assign(...args);
}
Util.extend(Util, {
    noop() {},
    /**
     * string | number | math
     */
    type(obj) {
        return (obj === null || obj === undefined) ? String(obj) : Object.prototype.toString.call(obj).match(/\[object (\w+)\]/)[1].toLowerCase();
    },
    keys(obj) {
        // TODO ,it contains attr from prototype clain 
        return Object.keys(obj);
    },
    // 
    extend: (...args) => {
        Object.assign(...args);
    },
    getValue(o, key) {
        if (this.type(o) === "object") {
            let value = o;
            const attrs = key.split(".");
            attrs.forEach(atrr => {
                if (value) {
                    value = value[atrr];
                }
            });
            return value;
        }
        return null;
    }
})

// type
"Boolean Number String Function Array Date RegExp Object Error Symbol".split(' ').forEach((value) => {
    Util[`is${value}`] = function(obj) {
        return Util.type(obj) === value.toLowerCase()
    }
})

Util.extend(Util, {
    isPlainObject(obj) {
        return Object.prototype.toString.call(obj) === "[object Object]";
    },
    hasOwn(obj, key) {
        return Object.prototype.hasOwnProperty.call(obj, key);
    }
})

Util.extend(Util, {
    EventBase,
    Event,
    log4j,
    Drag
})


// proxy
let sharedPropertyDefinition = {
    enumerable: true,
    configurable: true,
    get: Util.noop,
    set: Util.noop
};

Util.extend(Util, {
    /**
     * 
     * @param {Object} target (vm)get/set 
     * @param {String} sourcekey   _data
     * @param {String} key   
     */
    proxy(target, sourceKey, key) {
        sharedPropertyDefinition.get = function proxyGetter() {
            return this[sourceKey][key]
        };
        sharedPropertyDefinition.set = function proxySetter(val) {
            this[sourceKey][key] = val;
        };
        Object.defineProperty(target, key, sharedPropertyDefinition);
    }
})


export default Util